import React from "react";
import {connect} from "react-redux";
import ProductListOfferPriceTag from "eshop/modules/simfree/components/filter/view/ProductListOfferPriceTag"
import VasPackages from "eshop/modules/configurator/components/lp/offers/VasPackages";
import PropositionDetailsForDevice from "eshop/modules/simfree/components/PropositionDetailsForDevice";
import PropositionFeatureForDevice from "eshop/modules/simfree/components/PropositionFeatureForDevice";
import {isProductDetailsPage} from "../selectors";


class PropositionForDeviceTabletDesktopView extends React.Component {
    getOptionalDetails() {
        if (this.props.entryUnderChange) {
            let details;
            try {
                details = JSON.parse(this.props.entryUnderChange.details)
            } catch (err) {
                console.log("## details on cart parsing error##", err)
            }
            return {
                data: details
            }
        } else {
            return {}
        }
    }


    isClientContext() {
        return this.props.clientContext || (this.props.contractRole ? true : false)
    }



    render() {
        let proposition = this.props.offer;
         return (
            <div className="l-row u-no-margin">
                {!this.props.addTerminalToOfferBundleNo &&
                <div className="l-col l-col-1 u-no-padding-left">
                    <label className="o-radio opl-radio u-small-hide" onClick={(e) => e.preventDefault()}>
                        <input type="radio" name={this.props.inputName || "offerPlans"} className="switcher-trigger"
                               checked={this.props.isSelected}
                               value={proposition.id}/>
                        <span className="o-ci"/>
                        <span className="o-ci-label"/>
                    </label>
                </div>
                }
                <div className="l-col l-col-4    u-small-no-padding-r u-no-padding-left">
                    <p>{proposition.rateplanName}</p>
                    {!!this.props.isProductDetailsPage &&
                    <PropositionDetailsForDevice {...proposition}
                                                 proposition={this.props.entryUnderChange || proposition}
                                                 clientContext={this.isClientContext()} {...this.getOptionalDetails()}/>
                    }
                </div>

                <div
                    className={"l-col " +(this.props.addTerminalToOfferBundleNo?" l-col-5":"l-col-4")+  " u-small-no-padding-r"}>
                    <div className={"l-row"}>
                        <div className={"l-col l-col-12"}>
                            {proposition.features &&
                            <PropositionFeatureForDevice
                                proposition={proposition}
                            />}
                        </div>
                        <div className={"l-col l-col-12"}>
                            <VasPackages offer={proposition} key={"vasPackagesLargeMedium"} changable={!this.props.addTerminalToOfferBundleNo}/>
                        </div>
                    </div>
                </div>
                <div className="l-col  l-col-3  u-block u-no-padding-r">

                    <ProductListOfferPriceTag
                        proposition={this.props.offer}
                        clientContext={this.props.clientContext}
                        contractRole={this.props.contractRole}
                        loyaltyLength={this.props.loyaltyLength}
                        alignClass={"u-right"}
                        mainPriceClass={""}
                        monthlyString="mies."
                        offerDiscountInfoDescription={this.props.offerDiscountInfoDescription}
                        offerDiscountInfoConvDescription={this.props.offerDiscountInfoConvDescription}
                        baseId="desktop"
                        key={"desktop_"+(!!this.props.clientContext?"clientContextOn":"clientContextOff")}
                    />

                </div>


            </div>
        )
    }
}

const mapStateToProps = state => ({
    isProductDetailsPage:isProductDetailsPage(state),
});

const mapDispatchToProps = dispatch => ({

});

export default connect(mapStateToProps, mapDispatchToProps)(PropositionForDeviceTabletDesktopView)

